<template>
  <div class="extractReceipts">
    <div class="filter">
      <el-input class="item" v-model="receiptsNum" placeholder="输入单号"></el-input>
      <el-input class="item" v-model="receiptsCode" placeholder="输入商品编码/商品名"></el-input>
      <el-input class="item" v-model="memberName" placeholder="输入会员姓名/手机号"></el-input>
      <el-date-picker
        v-model="dateRange"
        type="daterange"
        align="right"
        unlink-panels
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        class="daterange item"
        :picker-options="pickerOptions">
      </el-date-picker>
      <el-button type="primary" class="searchBtn item">搜索</el-button>
    </div>
    <el-table
      :data="receiptsList"
      style="width: 100%;"
      class="receiptsList"
      highlight-current-row
      @current-change="handleCurrentChange"
    >
      <el-table-column type="expand">
        <template slot-scope="scope">
          <div class="goodsList">
            <div class="goods" v-for="(item,idx) in scope.row.goodsList" :key="idx">
              <p>{{item.name}}/{{item.num}}/{{item.size}}/{{item.company}}</p>
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="单号" prop="num" width="120px"></el-table-column>
      <el-table-column label="挂单时间" width="160px" prop="time"></el-table-column>
      <el-table-column label="顾客" prop="customer"></el-table-column>
      <el-table-column label="收银员" prop="handler"></el-table-column>
      <el-table-column label="实收金额" prop="count"></el-table-column>
    </el-table>
    <div class="btns">
      <el-button class="btn" round @click.prevent="handleDelete">删除</el-button>
      <el-button class="btn" type="primary" round @click.prevent="handleExtract">提取</el-button>
    </div>
  </div>
</template>

<script>
    import {pickerOps, receiptsList} from "@/utils/staticData";

    export default {
        name: "extractReceipts",
        data() {
            return {
                pickerOptions: pickerOps,
                receiptsNum: '',
                receiptsCode: '',
                memberName: '',
                dateRange: '',
                receiptsList: receiptsList,
                chooseId: ''
            }
        },
        methods: {
            handleDelete() {
                if(!this.chooseId){
                    this.$alert('请选择想要删除的选项', '提示', {
                        type: 'warning',
                        center: true,
                        showConfirmButton: false,
                        callback: ()=>{
                            return true
                        }
                    })
                }else{
                    this.$confirm('是否删除当前项目?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        let index
                        this.receiptsList.some((item, idx) => {
                            if (item.num === this.chooseId) {
                                index = idx
                            }
                            return item.num === this.chooseId
                        })
                        this.receiptsList.splice(index, 1)
                    }).catch((e) => {
                        // console.error(e);
                    });
                }
            },
            handleExtract(){
                if(!this.chooseId){
                    this.$alert('请选择想要删除的选项', '提示', {
                        type: 'warning',
                        center: true,
                        showConfirmButton: false,
                        callback: ()=>{
                            return true
                        }
                    })
                }else{

                }
            },
            handleCurrentChange(val) {
                this.chooseId = val.num
            }
        }
    }
</script>

<style lang="scss" scoped>
  .extractReceipts {
    .filter {
      display: flex;
      flex-wrap: wrap;

      .item {
        width: 30%;
        margin-right: 10px;
        margin-bottom: 10px;

        &:last-child {
          margin-right: 0;
        }

        &.daterange {
          width: 40%;
        }
      }

      /deep/ {
        .el-autocomplete, .el-input {
          height: 40px;
          line-height: 40px;
          background-color: #ffffff;

          input {
            height: 100%;
            line-height: 100%;
            border-radius: 0;
          }
        }

        .el-date-editor {
          width: 200px;
        }
      }
    }

    .receiptsList {
      .goodsList {
        .goods {
          line-height: 30px;
        }
      }
    }

    .btns {
      padding-top: 20px;
      text-align: center;

      .btn {

      }
    }
  }
</style>



