<template>
  <div class="returnReceipts">
    <div class="filter">
      <el-input class="item" v-model="receiptsNum" placeholder="输入单号"></el-input>
      <el-input class="item" v-model="receiptsCode" placeholder="输入商品编码/商品名"></el-input>
      <el-input class="item" v-model="memberName" placeholder="输入会员姓名/手机号"></el-input>
      <el-date-picker
        v-model="dateRange"
        type="daterange"
        align="right"
        unlink-panels
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
        class="daterange item"
        :picker-options="pickerOptions">
      </el-date-picker>
      <el-button type="primary" class="searchBtn item">搜索</el-button>
    </div>
    <el-table
      :data="receiptsList"
      style="width: 100%;"
      class="receiptsList"
      max-height="400"
      highlight-current-row
      @current-change="handleCurrentChange"
    >
      <el-table-column label="单号" prop="num" width="120px"></el-table-column>
      <el-table-column label="时间" prop="time" width="160px"></el-table-column>
      <el-table-column label="顾客" prop="customer"></el-table-column>
      <el-table-column label="收银员" prop="handler"></el-table-column>
      <el-table-column label="支付方式" prop="pay"></el-table-column>
      <el-table-column label="实收金额" prop="count"></el-table-column>
    </el-table>
    <el-table
      :data="returnList"
      style="width: 100%;"
      class="returnList"
      max-height="250"
    >
      <el-table-column label="品名" width="250px">
        <template slot-scope="scope">
          <div class="categories">
            <p>{{scope.row.name}}/{{scope.row.num}}</p>
            <i>{{scope.row.size}}/{{scope.row.company}}</i>
          </div>
        </template>
      </el-table-column>
      <el-table-column label="批号/效期至" width="120px">
        <template slot-scope="scope">
          <i>{{scope.row.batchNum}}</i><br>
          <i>{{scope.row.expiryDate}}</i>
        </template>
      </el-table-column>
      <el-table-column label="购买数量" prop="amount"></el-table-column>
      <el-table-column label="实价" prop="price"></el-table-column>
      <el-table-column label="购买小计">
        <template slot-scope="scope">
              <i>{{(+scope.row.amount * +scope.row.price).toFixed(2)}}</i>
        </template>
      </el-table-column>
      <el-table-column label="退货数量" width="120px">
        <template slot-scope="scope">
          <el-input-number
            class="number-input"
            v-model="scope.row.returns"
            size="mini"
            :min="0"
            @change="getChooseChange(scope.row, scope.$index)"
          >
          </el-input-number>
        </template>
      </el-table-column>
      <el-table-column label="退货小计">
        <template slot-scope="scope">
          <i>{{scope.row.returns ? (+scope.row.returns * +scope.row.price).toFixed(2) : 0}}</i>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button size="mini" type="primary" @click="returnGoods(scope.row)">退货</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
    import {pickerOps, receiptsList} from "@/utils/staticData";

    export default {
        name: "returnReceipts",
        data() {
            return {
                pickerOptions: pickerOps,
                receiptsNum: '',
                receiptsCode: '',
                memberName: '',
                dateRange: '',
                receiptsList: receiptsList,
                returnList: [],
                returns: {}
            }
        },
        created(){
            this.$emit('changeDialogWidth', '70%')
            this.receiptsList.forEach(item => {
                Array.isArray(item.goodsList) && item.goodsList.forEach(val=>{
                  val.returns = val.amount
              })
            })
        },
        methods: {
            handleCurrentChange(val) {
                this.returnList = val.goodsList
            },
            returnGoods(val){
              this.returns = val
            },
            getChooseChange(row, index){
                console.log(row.returns, 'row.returns')
                this.returnList[index] = row
            }
        }
    }
</script>

<style lang="scss" scoped>
  .returnReceipts {
    .filter {
      display: flex;
      flex-wrap: wrap;

      .item {
        width: 30%;
        margin-right: 10px;
        margin-bottom: 10px;

        &:last-child {
          margin-right: 0;
        }

        &.daterange {
          width: 40%;
        }
      }

      /deep/ {
        .el-autocomplete, .el-input {
          height: 40px;
          line-height: 40px;
          background-color: #ffffff;

          input {
            height: 100%;
            line-height: 100%;
            border-radius: 0;
          }
        }

        .el-date-editor {
          width: 200px;
        }
      }
    }
    .returnList {
      .categories {
        /*line-height: 30px;*/
        p{
          /*font-size: 16px;*/
          font-weight: bold;
          overflow: hidden;
          text-overflow:ellipsis;
          white-space: nowrap;
        }
      }
      .number-input{
        width: 80px;
      }
    }
  }
</style>



